// import React,{useState} from 'react'

export default function About(props) {
    // const [myStyle,setStyle]=useState({
    //     color:'black',
    //     backgroundColor:'white',
    //     border:'1px solid black'
    // });
    // const [btnText,setBtnText]=useState("Dark Mode");
    // let toggleMode=()=>{
    //     if(props.mode==='light'){
    //         setStyle({
    //             color:'white',
    //             backgroundColor:'black',
    //             border:'1px solid white'
    //         });
    //         setBtnText("Light Mode");
    //     }else{
    //         setStyle({
    //             color:'black',
    //             backgroundColor:'white',
    //             border:'1px solid black'
    //         });
    //         setBtnText("Dark Mode");
    //     }
    // };

    // only this could be used to set mode

    let themeIdx=props.theme.idx;
    let color=props.theme.colors[themeIdx].color;

    const dark={
        color:color,
        backgroundColor:'black',
        border:'1px solid white'
    };
    const light={
        color:'white',
        backgroundColor:color,
        border:'1px solid black'
    };

    return (
        <div className="container" style={props.mode==='light'?light:dark}>
            <h1 className="my-3">About Us</h1>
            <div className="accordion" id="accordionExample">
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingOne">
                    <button className="accordion-button" type="button" style={props.mode==='light'?light:dark} data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                        Accordion Item #1
                    </button>
                    </h2>
                    <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                    <div className="accordion-body" style={props.mode==='light'?light:dark}>
                        <strong>This is the first item's accordion body.</strong> It is shown by default, until the collapse plugin adds the appropriate classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
                    </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingTwo">
                    <button className="accordion-button collapsed" type="button" style={props.mode==='light'?light:dark} data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                        Accordion Item #2
                    </button>
                    </h2>
                    <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
                    <div className="accordion-body" style={props.mode==='light'?light:dark}>
                        <strong>This is the second item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
                    </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingThree">
                    <button className="accordion-button collapsed" type="button" style={props.mode==='light'?light:dark} data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                        Accordion Item #3
                    </button>
                    </h2>
                    <div id="collapseThree" className="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
                    <div className="accordion-body" style={props.mode==='light'?light:dark}>
                        <strong>This is the third item's accordion body.</strong> It is hidden by default, until the collapse plugin adds the appropriate classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It's also worth noting that just about any HTML can go within the <code>.accordion-body</code>, though the transition does limit overflow.
                    </div>
                    </div>
                </div>
            </div>
            {/* <button class="btn btn-primary my-2" onClick={toggleModeDirect}>{btnText}</button> */}
        </div>
    )
}
